$color: #ccaa00;
$font: 'Fjalla One', sans-serif;
$count-color: rgba(204, 170, 0, 0.2);
$back-box-color: #1a2336c7;

body {
    background-image: url('../images/background-space.jpg');
}

.hexagon-box {
    width: 100vh;
    height: 100vh;
    margin: auto;
}

.cell, .cell-toggle {
    width: 14vh;
    height: 12vh;
    position: relative;
    text-align: center;
    -webkit-clip-path: polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);
            clip-path: polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);
    background-color: $color;
    cursor: pointer;
}

.cell {
    visibility: hidden;
    z-index: 1;
    transition: 1s;
}

.cell-toggle {
    z-index: 1;
    top: 41%;
    left: 0%;
    right: 0;
    bottom: 0;
    margin: auto;
    visibility: visible;
}

.cells {
    top: 41%;
    left: 0%;
    right: 0;
    bottom: 0;
    margin: auto;
}

.j-webb-box {
    position: relative;
    top: -13vh;
    bottom: 0;
    left: 0;
    right: 0;
}

#j-webb {
    visibility: hidden;
    color: white;
    font-size: 3.5vh;
    opacity: .7;
    font-family: $font;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 10vh;
    height: 10vh;
    position: absolute;
    z-index: 2;
    left: 45vh;
    top: 43vh;
    
    @-webkit-keyframes scaleTitle {
        0% {
            visibility: visible;
            font-size: 100vh;
            opacity: .6;
        }
        100% {
            visibility: visible;
            font-size: 3.5vh;
            opacity: .6;
        }
    }
    
    @keyframes scaleTitle {
        0% {
            visibility: visible;
            font-size: 100vh;
            opacity: .6;
        }
        100% {
            visibility: visible;
            font-size: 3.5vh;
            opacity: .6;
        }
    }

}



#count-timer {
    display: flex;
    position: relative;
    width: 100%;
    height: 100%;
    font-size: 2vh;
    font-weight: bold;
    align-items: center;
    justify-content: center;
    visibility: hidden;
}

#countdown {
    width: 30%;
    height: 18%;
    color: white;
    position: absolute;
    margin: auto;
    top: -90%;
    bottom: 0;
    left: -70%;
    right: 0;
    display: grid;
    grid-template-rows: 6vh 10vh;
    row-gap: 1vh;
    text-align: center;
    align-items: center;
    background-color: $back-box-color;
    border-radius: 0.1vh;
    border: 1px solid white;
    font-family: $font;
    visibility: hidden;

    #bg-img {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: -1;
        background: url('../images/launch-img.png');
        background-size: 100% auto;
        width: 100%;
        height: auto;
        opacity: .7;
    }
    
    .title {
        font-size: 4vh;
        padding: 2%;
        padding-top: 5%;
    }
    .count-box {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        font-size: 3vh;
    }
    
    #count-timer-max {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        font-size: 4vh;
        padding: 2%;
    }
}


